import React from 'react';
import classes from './GradientCard.module.scss';
import { Progress } from 'antd';

function GradientCard({ color, text, img, mainNumber, percentageImg, percent }) {
	const handleClass = () => {
		switch (color) {
			case 'blue':
				return classes.blue;
			case 'red':
				return classes.red;
			case 'green':
				return classes.green;
			case 'orange':
				return classes.orange;
			default:
				return classes.red;
		}
	};
	const cls = handleClass();
	return (
		<div className={`${classes.back} ${cls}`}>
			<div className="d-flex justify-content-between pt-3 pb-1 pr-3 pl-3">
				<div className="mt-1">{mainNumber.toLocaleString('ar-EG') || 250}</div>
				<div>
					<img className={classes.cart} alt="cart-icon" src={require(`../../img/${img}.png`)} />
				</div>
			</div>
			<Progress
				strokeColor="white"
				trailColor="#CDCDCD"
				className="pl-3 pr-3"
				showInfo={false}
				percent={percent}
				size="small"
			/>
			<div className="d-flex justify-content-between pb-3 pt-2 pl-3 pr-3">
				<div>{text || 'مجموع'}</div>
				<div>
					{`% ${percent.toLocaleString('ar-EG')}` || '3'}
					<img
						className={classes.percentageImg}
						alt="percentage"
						src={require(`../../img/${percentageImg}.png`)}
					/>
				</div>
			</div>
		</div>
	);
}

export default GradientCard;
