import React, { Component } from 'react';
import classes from './Sidebar.module.scss';
import Context from '../../Context';
import { Menu } from 'antd';
import { PieChartOutlined, DesktopOutlined, MailOutlined } from '@ant-design/icons';

const { SubMenu } = Menu;
export class Sidebar extends Component {
	state = {
		collapsed: false
	};

	toggleCollapsed = () => {
		this.setState({
			collapsed: !this.state.collapsed
		});
	};
	render() {
		return (
			<Context.Consumer>
				{(value) => (
					<div className={value.sidebar ? classes.sidebar : classes.closedsidebar}>
						<Menu
							dir="rtl"
							className={classes.sidebarMedia}
							mode="inline"
							theme="dark"
							inlineCollapsed={!value.sidebar}
						>
							<SubMenu className="text-right  " key="sub1" icon={<MailOutlined />} title="منوی یک">
								<Menu.Item className="text-right " key="1">
									گزینه یک
								</Menu.Item>
								<Menu.Item className="text-right " key="2">
									گزینه دو
								</Menu.Item>
							</SubMenu>
							<Menu.Item className="text-right" dir="rtl" key="3" icon={<PieChartOutlined />}>
								گزینه سه
							</Menu.Item>
							<Menu.Item className="text-right" key="4" icon={<DesktopOutlined />}>
								گزینه چهار
							</Menu.Item>
						</Menu>
					</div>
				)}
			</Context.Consumer>
		);
	}
}

export default Sidebar;
