import React from 'react';
import classes from './WideCard.module.scss';
import { Progress } from 'antd';

function WideCard({ title, desc, img, percentage }) {
	return (
		<div className={classes.card}>
			<Progress className="p-3" type="circle" percent={percentage || 75} width={80} />
			<div className="d-flex justify-content-between align-items-center w-100">
				<div className="d-flex flex-column justify-content-center">
					<h4 className="p-0 m-0">{title || 'فالور ها'}</h4>
					<p className="p-0 m-0 text-secondary">{desc || 'از فروردین'}</p>
				</div>
				<div className="mr-5">
					<img alt="logo" className={classes.logo} src={require(`../../img/${img}.png`)} />
				</div>
			</div>
		</div>
	);
}

export default WideCard;
