import React from 'react';
import WideCard from './WideCard/WideCard';

function WideCards() {
	return (
		<React.Fragment>
			<div className="col-lg-4 col-12 d-flex justify-content-center align-items-center">
				<WideCard title="فیسبوک" desc="از اردیبهشت" percentage={43} img={`facebook`} />
			</div>
			<div className="col-lg-4 col-12 d-flex justify-content-center align-items-center">
				<WideCard title="اینستاگرام" desc="از تیر" percentage={67} img={`instagram`} />
			</div>
			<div className="col-lg-4 col-12 d-flex justify-content-center align-items-center">
				<WideCard title="توییتر" desc="از مرداد" percentage={11} img={`twitter`} />
			</div>
		</React.Fragment>
	);
}

export default WideCards;
