import React from 'react';
import classes from './SidebarAndOther.module.scss';
import Sidebar from '../Sidebar/Sidebar';
import Footer from '../Footer/Footer';
import ChartAndBar from '../ChartAndBar';
import GradientCards from '../GradientCards';
import WideCards from '../WideCards';

function SidebarAndOther() {
	return (
		<React.Fragment>
			<div className="d-flex">
				<div className={`container ${classes.sidebarandother}`}>
					<div className="row d-flex justify-content-center align-items-center">
						<GradientCards />
					</div>
					<div className="row d-flex justify-content-center align-items-center mt-4">
						<ChartAndBar />
					</div>
					<div className="row d-flex justify-content-center align-items-center mt-4 pb-5">
						<WideCards />
					</div>
				</div>
				<Sidebar />
			</div>
			<Footer />
		</React.Fragment>
	);
}

export default SidebarAndOther;
