import React from 'react';
import GradientCard from './GradientCard/GradientCard';

function GradientCards() {
	return (
		<React.Fragment>
			<div className="col-lg-3 col-md-3 col-sm-12 col-12 d-flex justify-content-center align-items-center mt-4">
				<GradientCard
					text="بازدید"
					mainNumber={124000}
					img={`eye`}
					color="orange"
					percentageImg={`up`}
					percent={14}
				/>
			</div>
			<div className="col-lg-3 col-md-3 col-sm-12 col-12 d-flex justify-content-center align-items-center mt-4 ">
				<GradientCard
					text="درآمد"
					mainNumber={3450}
					img={`dollar`}
					color="green"
					percentageImg={`up`}
					percent={76}
				/>
			</div>
			<div className="col-lg-3 col-md-3 col-sm-12 col-12 d-flex justify-content-center align-items-center mt-4 ">
				<GradientCard
					text="پیام ها"
					mainNumber={4}
					img={`message`}
					color="blue"
					percentageImg={`up`}
					percent={45}
				/>
			</div>
			<div className="col-lg-3 col-md-3 col-sm-12 col-12 d-flex justify-content-center align-items-center mt-4">
				<GradientCard
					percentageImg={`down`}
					text="خرید ها"
					mainNumber={5}
					img={`shop`}
					color="red"
					percent={89}
				/>
			</div>
		</React.Fragment>
	);
}

export default GradientCards;
