import React from 'react';
import classes from './Footer.module.scss';

function Footer() {
	const number = 20;
	const name = 'مهدی فرجی';
	return (
		<div className={classes.footer}>
			<p className="pt-3 text-light">{`${name} ${number.toLocaleString('ar-EG')}${number.toLocaleString(
				'ar-EG'
			)}`}</p>
		</div>
	);
}

export default Footer;
