import classes from './Chart.module.scss';
import React, { Component } from 'react';
import ReactApexChart from 'react-apexcharts';

export class Chart extends Component {
	constructor(props) {
		super(props);

		this.state = {
			close: false,
			series: [
				{
					name: 'چارت یک',
					data: [
						Math.floor(Math.random() * 190),
						Math.floor(Math.random() * 430),
						Math.floor(Math.random() * 240),
						Math.floor(Math.random() * 470),
						Math.floor(Math.random() * 610),
						Math.floor(Math.random() * 350),
						Math.floor(Math.random() * 870)
					]
				}
			],
			options: {
				colors: [ '#F44336' ],
				chart: {
					height: 150,
					type: 'area'
				},
				dataLabels: {
					enabled: false
				},
				stroke: {
					curve: 'smooth'
				},
				xaxis: {
					type: 'datetime',
					categories: [
						'2018-09-19T00:00:00.000Z',
						'2018-09-19T01:30:00.000Z',
						'2018-09-19T02:30:00.000Z',
						'2018-09-19T03:30:00.000Z',
						'2018-09-19T04:30:00.000Z',
						'2018-09-19T05:30:00.000Z',
						'2018-09-19T06:30:00.000Z'
					]
				},
				tooltip: {
					x: {
						format: 'dd/MM/yy HH:mm'
					}
				}
			}
		};
	}

	render() {
		const { mainTitle, titleOne, titleTwo, titleThree, descOne, descTwo, descThree } = this.props;
		const handleClose = () => {
			if (this.state.close) {
				return classes.closeTrue;
			}
			return '';
		};
		const cls = handleClose();
		return (
			<React.Fragment>
				<div className={`${classes.chart} ${cls}`}>
					<div className="p-3 d-flex justify-content-between">
						<h4 className="mt-2 ml-4">{mainTitle || 'چارت'}</h4>
						<div onClick={() => this.setState({ close: true })}>
							<img alt="close" className={classes.close} src={require('../../img/close.png')} />
						</div>
					</div>
					<div className="pl-3 pr-3">
						<ReactApexChart
							options={this.state.options}
							series={this.state.series}
							type="area"
							height={200}
						/>
					</div>
					<div className="d-flex justify-content-center pb-4 pt-2">
						<div className="d-flex flex-column align-items-center">
							<h4>{titleOne || 'عنوان یک'}</h4>
							<p className="p-0 m-0 text-secondary">{descOne || 'توضیحات یک'}</p>
						</div>
						<div className="d-flex flex-column align-items-center mr-4 ml-4">
							<h4>{titleTwo || 'عنوان دو'}</h4>
							<p className="p-0 m-0 text-secondary">{descTwo || 'توضیحات دو'}</p>
						</div>
						<div className="d-flex flex-column align-items-center">
							<h4>{titleThree || 'عنوان سه'}</h4>
							<p className="p-0 m-0 text-secondary">{descThree || 'توضیحات سه'}</p>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default Chart;
